@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  background: radial-gradient(circle at 50% 300px, #26fafe, #c40ce9 100%),
  #ef99fa;
}

.glowy {
  box-shadow: 80px 40px 75px hsla(0, 0%, 100%, 0.9),
    -80px 40px 75px hsla(0, 0%, 100%, 0.9),
    -80px -40px 75px hsla(0, 0%, 100%, 0.9);
}
.title-bar-text {
  color: #fff;
  font-weight: 700;
  letter-spacing: 0;
  margin-right: 24px;
  box-shadow: 3px 0 4px rgba(0, 0, 0, 0.5),
    /* Left side shadow */ 5px 0 21px rgba(0, 0, 0, 0.5),
    /* Right side shadow */ 0 2px 3px rgba(0, 0, 0, 0.5);

  margin-left: 30px;
}
.title-bar {
  align-items: center;
  background: linear-gradient(90deg, navy, #1084d0);
  display: flex;
  justify-content: space-between;
  padding: 3px 2px 3px 3px;
}
table {
  text-indent: 0;
  border-color: inherit;
  border-collapse: collapse;
}
.pop{
  pointer-events: none;
}
.pop button{
  pointer-events: auto;
}


@media screen and (max-width: 768px) {
  .App {
    height: 340vh;
  }
}
